

.movie-section {
  display: flex;
  justify-content: space-evenly;
  padding: 40px 0px;
  color: #fafafa;
  font-weight: 400;
}

.movie-rating {
  padding-left: 3px;
  margin-top: 20px;
  color:#79b8f3;
  display: flex;
  
}
span {
  margin-right: 20px;
}

.movie-title {
  font-size: 40px;
  color: #fafafa;
}
.movie-plot {
  margin-top: 20px;
  line-height: 1.8rem;
}

.movie-info > div span:first-child {
  padding: 10px 0px;
  color: #fafafa;
  font-weight: 600;
  width: 100px;
  display: inline-block;
}

.movie-info > div span {
  color: #79b8f3;
}

.fa-star {
  color: #ff9e00;
}
.fa-thumbs-up {
  color: #fafafa;
}

.fa-film {
  color: rgb(191, 213, 214);
}

.fa-calendar {
  color: peachpuff;
}

.section-right {
  margin-left: 30px;
}
.trailer{
  display :flexbox;
  flex-direction: row;
  justify-content: end;
  position :relative;
 margin-top: 20px;
  width :15%;
  padding : 4px 8px;
  border-radius:4px;
  background-color:crimson;
}
